import gsap from 'gsap'

class About
{
    constructor(experience)
    {
        this.experience = experience

        this.openButton = document.querySelector('.about-btn')
        this.skillsButton = document.querySelector('.about-skills')
        this.closeButton = document.querySelector('.close-about-btn')
        this.closeSkillsButton = document.querySelector('.close-skills-btn')

        this.createOpenClick()
        this.createCloseClick()
    }

    // Event listener for "Open about page"
    createOpenClick()
    {
        this.openButton.addEventListener('click',()=>
        {
            // Disable scroll and hide HTML elements
            document.body.style.overflowY = 'hidden'
            gsap.to('.links-wrapper', {duration:0.2, opacity:0})
            gsap.to('.progress', {duration:0.2, opacity:0})
            gsap.to('.scroll-call', {duration:0.2, opacity:0})
            gsap.to('.section1', {duration:0.2, opacity:0})
            gsap.to('.nav', {duration:0.2, opacity:0})
            document.querySelector('.about').style.display = 'flex'

            // Hide project meshes
            for(const mesh of this.experience.world.cylinder.meshesArray)
            {
                mesh.visible = false
            }

            this.experience.world.font.about.visible = true
            this.experience.renderer.unrealBloom.strength = 0

            if(this.experience.sizes.width < 600)
            {
                gsap.to(this.experience.camera.instance.position, {duration:1, x:1.5, y:0, z:0})

            }
            else 
            {
                gsap.to(this.experience.camera.instance.position, {duration:1, x:1, y:0, z:0})
            }
        })
    }

    // Event listener for "Close about page"
    createCloseClick()
    {
        this.closeButton.addEventListener('click',()=>
        {
            // Enable scroll and show HTML elements
            document.body.style.overflowY = 'auto'
            gsap.to('.links-wrapper', {duration:0.2, opacity:1})
            gsap.to('.progress', {duration:0.2, opacity:1})
            gsap.to('.scroll-call', {duration:0.2, opacity:1})
            gsap.to('.section1', {duration:0.2, opacity:1})
            gsap.to('.nav', {duration:0.2, opacity:1})
            document.querySelector('.about').style.display = 'none'

            this.experience.world.font.about.visible = false
            this.experience.renderer.unrealBloom.strength = 1

            gsap.to(this.experience.camera.instance.position, {duration:1, x:0, y:0, z:2})
        })
    }
}

export default About