import * as THREE from 'three'

import backVertex from '../../shaders/backVertex.glsl'
import backFragment from '../../shaders/backFragment.glsl'

class Background
{
    constructor(experience)
    {
        this.experience = experience

        this.createBackground()
        this.createRing()
    }

    createBackground()
    {
        this.backgroundGeometry = new THREE.SphereBufferGeometry(10, 10, 10)
        this.backgroundMaterial = new THREE.ShaderMaterial({
            side: THREE.DoubleSide,
            transparent:true,
            depthTest: false,
            depthWrite: false,
            blending: THREE.AdditiveBlending,
            vertexShader: backVertex,
            fragmentShader: backFragment,
            uniforms:{
                uTime:{value:0},
                uPointsTexture:{ value: this.experience.resources.items.pointsTexture}
            }
        })

        this.backgroundMesh = new THREE.Points(this.backgroundGeometry,this.backgroundMaterial)

        this.experience.scene.add(this.backgroundMesh)
    }

    createRing()
    {
        this.ringGeometry =  new THREE.RingBufferGeometry(5,5.2,64,36)

        this.ring = new THREE.Mesh(this.ringGeometry, new THREE.MeshBasicMaterial({color:0x1112f3}))
        this.ring.position.z = -3

        this.experience.scene.add(this.ring)
    }

    update()
    {
        this.backgroundMesh.material.uniforms.uTime.value = this.experience.time.elapsed * 0.0001
        this.backgroundMesh.rotation.y = this.experience.time.elapsed * 0.0001
    }
}

export default Background