import gsap from 'gsap'
import * as THREE from 'three'

class Mouse 
{
    constructor(experience)
    {
        this.experience = experience

        // Raycaster and mouse coords
        this.raycaster = new THREE.Raycaster()
        this.mouse = new THREE.Vector2()
        this.intersects = null

        // DOM mouse circle
        this.mouseElement = document.querySelector('.mouse')

        // Instantiate methods
        this.mouseMove()
        this.mouseScroll()
    }

    // Listener for click events
    mouseClick()
    {
        window.addEventListener('click',()=>
        {
        })
    }

    // Animations on mousemove
    mouseMove()
    {
        window.addEventListener('mousemove' , (e)=>
        {
            this.mouse.x = ( e.clientX / window.innerWidth ) * 2 - 1
            this.mouse.y = - ( e.clientY / window.innerHeight ) * 2 + 1
            this.raycaster.setFromCamera( this.mouse, this.experience.camera.instance );

            // Move particles with mouse
            this.experience.world.background.backgroundMesh.position.x = this.mouse.x
            this.experience.world.background.backgroundMesh.position.y = -this.mouse.y

            // Transform circle with mouse movement
            // gsap.to(this.mouseElement, {direction:0.1, x:e.clientX - 46/2, y:e.clientY - 46/2})

            // Animate project intersections
            if(this.experience.world.cylinder.meshesArray)
            {
                for( const mesh of this.experience.world.cylinder.meshesArray)
                {
                this.intersects = this.raycaster.intersectObject(mesh)
                if(this.intersects.length)
                {
                    // gsap.to(mesh.material.uniforms.uColor, {duration:1, value:3})
                    gsap.to(mesh.material.uniforms.uImageTransition, {duration:1, value:1})
                }
                else
                {
                    // gsap.to(mesh.material.uniforms.uColor, {duration:1, value:1})
                    gsap.to(mesh.material.uniforms.uImageTransition, {duration:1, value:0})
                }
                }
            }

            // Intersections for work projects
            this.intersectsWork = this.raycaster.intersectObject(this.experience.world.secondCylinder.project)
            if(this.intersectsWork.length)
            {
                gsap.to(this.experience.world.secondCylinder.project.material.uniforms.uImageTransition, {duration:1,value:1})
                gsap.to(this.experience.world.secondCylinder.project.scale, {duration:1, x:1.5, y:1.5})
            }
            else
            {
                gsap.to(this.experience.world.secondCylinder.project.material.uniforms.uImageTransition, {duration:1,value:0})
                gsap.to(this.experience.world.secondCylinder.project.scale, {duration:1, x:1, y:1})
            }

        })
    }

    // Animations on scroll
    mouseScroll()
    {
        this.scroll = 0
        window.addEventListener('scroll',()=>
        {
            this.scroll = window.scrollY / this.experience.sizes.height
            let clipValue = (this.scroll * 10) * 1.1

            // Animate custom progress bar
            gsap.to('.progress', {duration:0.2, clipPath:`polygon(0 0, 100% 0, 100% ${clipValue}%, 0 ${clipValue}%)`})


            if(this.scroll <= 0.8)
            {
                this.showProgressLinks(0)
            }
            
            if(this.scroll > 0.8)
            {
                this.showProgressLinks(1)
            }
            
            if(this.scroll > 1.8)
            {
                gsap.to('.section2', {duration:0.1, clipPath:'polygon(0 100%, 100% 100%, 100% 0, 0 0)'})
                this.showProgressLinks(2)
                this.experience.world.group.rotation.y = (this.scroll - 1.8) * 1.1
                gsap.to(this.experience.world.font.header.position, {duration:0.5, y:-2})
                for(const mesh of this.experience.world.cylinder.meshesArray)
                {
                    mesh.visible = true
                }
            }

            if(this.scroll > 2.8)
            {
                gsap.to('#section4-content', {duration:0.2, y:0})
                this.animateTextures(1)  
                this.showProgressLinks(3)          
            }

            if(this.scroll > 3.8)
            {
                gsap.to('#section5-content', {duration:0.2, y:0})
                this.animateTextures(0)  
                this.showProgressLinks(4)          
            }

            if(this.scroll > 4.8)
            {
                gsap.to('#section6-content', {duration:0.2, y:0})
                this.animateTextures(4)  
                this.showProgressLinks(5)          
            }

            if(this.scroll > 5.8)
            {
                gsap.to('#section7-content', {duration:0.2, y:0})
                this.animateTextures(3)    
                this.showProgressLinks(6)          
            }

            if(this.scroll > 6.8)
            {
                // Show project meshes
                for(const mesh of this.experience.world.cylinder.meshesArray)
                {
                    mesh.visible = true
                }
                gsap.to('#section8-content', {duration:0.2, y:0})
                this.animateTextures(3)    
                this.showProgressLinks(7)          
            }

            if(this.scroll > 7.8)
            {
                // Hide project meshes
                for(const mesh of this.experience.world.cylinder.meshesArray)
                {
                    mesh.visible = false
                }

                this.animateTextures(2)
                this.showProgressLinks(8)          
                gsap.to(this.experience.camera.instance.position, {duration:0.5,x:0, z:1.5, y:1})
                
                gsap.to('.scroll-call', {duration:1, opacity:1})
            }
            else 
            {
                gsap.to(this.experience.camera.instance.position, {duration:0.5,x:0, z:2, y:0})                    
            }

            if(this.scroll > 8.8)
            {
                this.showProgressLinks(9)
                gsap.to(this.experience.camera.instance.position, {duration:0.5,x:1, z:0, y:0.2})                    
                gsap.to(this.experience.world.font.header.position, {duration:0.5, y:0})
                gsap.to(this.experience.world.model.model.rotation, {duration:0.5, y:-1})                    

                gsap.to('.scroll-call', {duration:1, opacity:0})
            }
            else
            {
                gsap.to(this.experience.world.model.model.rotation, {duration:0.5, y:0})                    
            }
        })
    }

    // Animate textures
    animateTextures(index)
    {
        const meshes = this.experience.world.cylinder.meshesArray

        for(const mesh of meshes)
        {
            gsap.to(mesh.material.uniforms.uColor, {duration:1, value:0})
        }
        gsap.to(meshes[index].material.uniforms.uColor, {duration:1, value:1})
    }

    // Handling the progress bar animation
    showProgressLinks(index)
    {
        this.links = document.querySelectorAll('.links')

        for(const link of this.links)
        {
            // gsap.to(link, {duration:0.5, opacity:0})
        }
        gsap.to(this.links[index], {duration:0.5, opacity:1})
    }
}

export default Mouse