import gsap from 'gsap'

class OpenAllWork
{
    constructor(experience)
    {
        this.experience = experience

        // Local properties
        this.counter = 1

        // Get all DOM elements
        this.openBtns = document.querySelectorAll('.open-all-work-btn')
        this.closeBtn = document.querySelector('.close-work-btn')
        this.nextBtn = document.querySelector('.next-project-btn')
        this.prevBtn = document.querySelector('.prev-project-btn')
        this.title = document.querySelector('.work-title')
        this.text = document.querySelector('.work-text')
        this.link = document.querySelector('.work-link')

        // Create an array of all project textures
        this.data = 
        [
            {
                texture:this.experience.resources.items.texture1,
                texture2:this.experience.resources.items.texture11,
                title:'Groundrock',
                text:`Website for a greek digital marketing business. I have developed the page from scratch using three.js & GLSL shaders for the visuals.`,
                url:'https://sizzysiks.github.io/groundrock_v4/'
            },
            {
                texture:this.experience.resources.items.texture2,
                texture2:this.experience.resources.items.texture22,
                title:'3D Customizer',
                text:`A personal experiment of an interactive product showcase. The user can view 
                the 3D product from all angles, alter specific parts of the product and order the finished
                version he creates.`,
                url:'https://sizzysiks.github.io/3d_customizer/'
            },
            {
                texture:this.experience.resources.items.texture3,
                texture2:this.experience.resources.items.texture33,
                title:'Portfolio 2021',
                text:`Personal portfolio that represented my work up to the point i've created the updated version.`,
                url:'https://antinokila.co.uk/'
            },
            {
                texture:this.experience.resources.items.texture4,
                texture2:this.experience.resources.items.texture44,
                title:'Anila',
                text:`interactive and immersive design for the portfolio of Anila Kila. Main points, the gltf model and 
                GLSL noisy background.`,
                url:'https://sizzysiks.github.io/anila_kila_final/'
            },
            {
                texture:this.experience.resources.items.texture5,
                texture2:this.experience.resources.items.texture55,
                title:'Physics',
                text:`Personal experiment, implementing physics into a 3D scene. The objects in the scene 
                fall from the sky, interract & bounce with each other as would objects governed by real
                life physics.`,
                url:'https://sizzysiks.github.io/physics/'
            },
            {
                texture:this.experience.resources.items.texture6,
                texture2:this.experience.resources.items.texture22,
                title:'React portfolio',
                text:`A portfolio for accountant Gabriel Ndoci. Minimalistic approach built with REACT.`,
                url:'https://sizzysiks.github.io/physics/'
            },
            {
                texture:this.experience.resources.items.texture7,
                texture2:this.experience.resources.items.texture77,
                title:'Pics to particles',
                text:`Transforming images into particles with complete control of each part. Mouse move or click creates a radius inside which the particles scatter in different speeds and space.`,
                url:'https://sizzysiks.github.io/pics-to-particles/'
            },
        ]

        // Initialise text
        this.title.textContent = this.data[0].title
        this.text.textContent = this.data[0].text
        this.link.href = this.data[0].url

        // Instantiate all methods
        this.createOpenClick()
        this.createCloseClick()
        this.createNextClick()
        this.createPrevClick()
    }

    // Event listener for Open Work button
    createOpenClick()
    {
        for(const btn of this.openBtns)
        {
            btn.addEventListener('click',()=>
            {
                gsap.to('.page-transition-overlay', {duration:0.5, x:0})

                // Transform overlay after all is set
                gsap.delayedCall(0.5, ()=>
                {
                    gsap.to('.work', {duration:0.5, x:0})

                    // Scroll to top
                    window.scrollTo({
                    top:0,
                    left:0,
                    behavior:'smooth'
                    })

                    // Hide previous elements
                    document.body.style.overflowY = 'hidden'

                    for(const mesh of this.experience.world.cylinder.meshesArray)
                    {
                        mesh.visible = false
                    }
                    
                    gsap.to('.section1', {duration:0.5, opacity:0})
                    gsap.to('.nav', {duration:0.5, opacity:0})
                    gsap.to('.progress', {duration:0.5, opacity:0})
                    gsap.to('.links-wrapper', {duration:0.5, opacity:0})
                    gsap.to('.scroll-call', {duration:0.5, opacity:0})

                    // Show projects, adjust camera and remove pass
                    this.experience.world.secondCylinder.project.visible = true
                    gsap.to(this.experience.camera.instance.position, {duration:0.5, z:2.5, y:0.5})
                    this.experience.world.model.model.rotation.y = -4.2
                    this.experience.renderer.unrealBloom.enabled = false
                })

                gsap.delayedCall(1,()=>
                {
                    gsap.to('.page-transition-overlay', {duration:0.5, x:-this.experience.sizes.width})
                })
            })
        }
    }

    // Event listener for Close Work button
    createCloseClick()
    {
        this.closeBtn.addEventListener('click',()=>
        {
                parent.location.hash = ''

                gsap.to('.page-transition-overlay', {duration:0.5, x:0})

                // Transform overlay after all is set
                gsap.delayedCall(0.5, ()=>
                {
                    gsap.to('.work', {duration:1, x:this.experience.sizes.width})

                    // Hide previous elements
                    document.body.style.overflowY = 'auto'
                    this.experience.world.group.visible = true
                    gsap.to('.section1', {duration:0.5, opacity:1})
                    gsap.to('.nav', {duration:0.5, opacity:1})
                    gsap.to('.progress', {duration:0.5, opacity:1})
                    gsap.to('.links-wrapper', {duration:0.5, opacity:1})
                    gsap.to('.scroll-call', {duration:0.5, opacity:1})

                    // Show projects, adjust camera and remove pass
                    this.experience.world.secondCylinder.project.visible = false
                    gsap.to(this.experience.camera.instance.position, {duration:0.5, z:2})
                    this.experience.renderer.unrealBloom.enabled = true
                })

                gsap.delayedCall(1,()=>
                {
                    gsap.to('.page-transition-overlay', {duration:0.5, x:this.experience.sizes.width})
                })
        })
    }

    // Event listener for Next Project button
    createNextClick()
    {
        this.nextBtn.addEventListener('click',()=>
        {
            this.animateMeshes()
            if(this.counter<this.data.length)
            {
                this.transformText(this.counter)
                this.experience.world.secondCylinder.project.material.uniforms.uTexture.value = this.data[this.counter].texture
                this.experience.world.secondCylinder.project.material.uniforms.uTexture2.value = this.data[this.counter].texture2
            }
            else
            {
                this.counter = 0
                this.experience.world.secondCylinder.project.material.uniforms.uTexture.value = this.data[0].texture
                this.experience.world.secondCylinder.project.material.uniforms.uTexture2.value = this.data[0].texture2
                this.transformText(0)
            }
            this.counter++
        })
    }

    // Event listener for Previous Project button
    createPrevClick()
    {
        this.prevBtn.addEventListener('click',()=>
        {
            this.animateMeshes()
            this.counter--
            if(this.counter>0)
            {
                this.experience.world.secondCylinder.project.material.uniforms.uTexture.value = this.data[this.counter].texture
                this.experience.world.secondCylinder.project.material.uniforms.uTexture2.value = this.data[this.counter].texture2
                this.transformText(this.counter)
            }
            else
            {
                this.counter = this.data.length-1
                this.experience.world.secondCylinder.project.material.uniforms.uTexture.value = this.data[this.counter].texture
                this.experience.world.secondCylinder.project.material.uniforms.uTexture2.value = this.data[this.counter].texture2
                this.transformText(this.counter)
            }
        })
    }

    // Changing text content and transform animations on next-prev click
    transformText(counter)
    {
        gsap.to(this.title, {duration:0.2, y:this.experience.sizes.height})
        gsap.to(this.text, {duration:0.4, y:this.experience.sizes.height})
        gsap.delayedCall(0.2,()=>
        {
                this.title.textContent = this.data[counter].title
                this.text.textContent = this.data[counter].text
                this.link.href = this.data[counter].url
                gsap.to(this.title, {duration:0.2, y:0})
                gsap.to(this.text, {duration:0.4, y:0})
        })
    }

    // Animate project meshes on nex-prev click
    animateMeshes()
    {
        gsap.to(this.experience.renderer.rgbShiftPass.uniforms.amount,{duration:0.2, value:0.1})
        gsap.to(this.experience.world.secondCylinder.project.scale,{duration:0.2,x:1.3,y:1.3})
        gsap.delayedCall(0.2,()=>
        {
            gsap.to(this.experience.renderer.rgbShiftPass.uniforms.amount,{duration:0.2, value:0})
            gsap.to(this.experience.world.secondCylinder.project.scale,{duration:0.2,x:1,y:1})
        })
    }
}

export default OpenAllWork