import gsap from 'gsap'

class Buttons
{
    constructor(experience)
    {
        this.experience = experience

        // DOM Elements
        this.scrollTopBtn = document.querySelector('.scroll-top-btn')
        this.talkBtn = document.querySelector('.talk-btn')

        // Instantiate methods
        this.createScrollTop()
        this.createTalkButtonClick()
    }

    // Scroll to top button
    createScrollTop()
    {
        this.scrollTopBtn.addEventListener('click',()=>
        {
            gsap.to('.page-transition-overlay', {duration:0.3, x:0})
            gsap.to('.page-transition-overlay', {delay:1, duration:0.3, x:this.experience.sizes.width})
            
            window.scrollTo({
                top:0,
                left:0,
                behavior:'smooth'
            })
        })
    }

    // Let's talk button
    createTalkButtonClick()
    {
        this.talkBtn.addEventListener('click',()=>
        {
            gsap.to('.page-transition-overlay', {duration:0.3, x:0})
            gsap.to('.page-transition-overlay', {delay:1, duration:0.3, x:this.experience.sizes.width})

            window.scrollTo({
                top:this.experience.sizes.height * 10,
                left:0,
                behavior:'smooth'
            })
        })
    }
}

export default Buttons