import * as THREE from 'three'
import gsap from 'gsap'
import {Reflector} from 'three/examples/jsm/objects/Reflector.js'

import Background from './Background.js'
import Cylinder from './Cylinder.js'
import Links from '../Functions/Links.js'
import Explore from '../Functions/Explore.js'
import Mouse from '../Utils/Mouse.js'

import Environment from './Environment.js'
import Model from './Model.js'
import Font from './Font.js'
import SecondCylinder from './SecondCylinder.js'
import OpenAllWork from '../Functions/OpenAllWork.js'

class World
{
    constructor(experience)
    {
        this.experience = experience
        this.scene = this.experience.scene
        this.resources = this.experience.resources

        this.group = new THREE.Group()
        this.scene.add(this.group)

        // Creating instaces after loading finishes
        this.resources.on('loaded' , ()=>{
            this.createLoadingFinished()
            this.createFloor()

            this.model = new Model(this.experience)
            this.font = new Font(this.experience)
            this.background = new Background(this.experience)
            this.cylinder = new Cylinder(this.experience)
            this.explore = new Explore(this.experience)
            this.environment = new Environment(this.experience)
            this.secondCylinder = new SecondCylinder(this.experience)
            this.mouse = new Mouse(this.experience)
            this.openAllWork = new OpenAllWork(this.experience)
        })
    }

    // Animations after loading has finished
    createLoadingFinished()
    {
        gsap.to('.loading-screen', {duration:0.5, background:'rgba(0,0,0,0)'})
        document.querySelector('.loading-text').style.display = 'none'
        document.querySelector('.enter-btn').textContent = 'ENTER'
    }

    // Create floor mesh
    createFloor()
    {
        this.floor = new Reflector( new THREE.CircleBufferGeometry(2,36,1), {
            map:this.experience.resources.items.floorNormal,
            clipBias: 0.1,
            textureWidth: window.innerWidth * window.devicePixelRatio,
            textureHeight: window.innerHeight * window.devicePixelRatio,
            color: 0x000011
        } );
        this.floor.position.y = -0.5
        this.floor.rotateX( - Math.PI / 2 );


        this.group.add(this.floor)
    }

    update()
    {
        if(this.secondCylinder)
        {
            this.secondCylinder.update()
        }

        if(this.background)
        {
            this.background.update()
        }
        
        if(this.model)
        {
            this.model.update()
        }
    }
}

export default World