import gsap from 'gsap'

class Explore
{
    constructor(experience)
    {
        this.experience = experience
        this.button = document.querySelector('.enter-btn')

        this.createClick()
    }

    createClick()
    {
        this.button.addEventListener('click',()=>
        {
            document.querySelector('.loading-screen').style.display = 'none'
            document.body.style.overflowY = 'auto'
            gsap.to('.nav', {duration:0.5, opacity:1})
            gsap.to('.section1', {duration:0.5, opacity:1})
            gsap.to('.progress', {duration:0.5, opacity:1})
            gsap.to('.links-wrapper', {duration:0.5, opacity:1})
            gsap.to('.scroll-call', {duration:0.5, opacity:1})
            gsap.to('.section1-title', {duration:1, y:0})
            gsap.to('.section1-text', {duration:1.2, y:0})
            gsap.to('.about-btn', {duration:1, y:0})
            gsap.to('.close-work-btn', {duration:1.2, y:0})

            gsap.to(this.button, {duration:0.5, opacity:0})

            if(this.experience.sizes.width < 600)
            {
                gsap.to(this.experience.camera.instance.position, {duration:1.5, x:0, y:0, z:2})
            }
            else
            {
                gsap.to(this.experience.camera.instance.position, {duration:1.5, x:0, y:0, z:1.5})
            }
        })
    }
}

export default Explore