import * as THREE from 'three'

import fragment from '../../shaders/projectsFragment.glsl'
import vertex from '../../shaders/projectsVertex.glsl'

class SecondCylinder
{
    constructor(experience)
    {
        this.experience = experience
        this.scene = this.experience.scene

        this.count = 3
        this.thetaLength = (2 * Math.PI) / this.count / 2
        this.offset = this.thetaLength + 0.6

        this.createProjects()
    }

    createProjects()
    {
        this.geo = new THREE.PlaneBufferGeometry(3.8,1.8,1)

        this.project = new THREE.Mesh(
            this.geo,
            new THREE.ShaderMaterial({
                side:THREE.DoubleSide,
                vertexShader:vertex,
                fragmentShader:fragment,
                uniforms:{
                    uTime:{value:0},
                    uTexture:{value:this.experience.resources.items.texture1},
                    uTexture2:{value:this.experience.resources.items.texture11},
                    uImageTransition:{value:0}
                }
            })
        )
        this.project.position.set(0,1,-2)
        this.project.visible = false

        this.experience.scene.add(this.project)
    }

    update()
    {
        this.project.material.uniforms.uTime.value = this.experience.time.elapsed * 0.0001
    }
}

export default SecondCylinder