import * as THREE from 'three'

import fragment from '../../shaders/fragment.glsl'
import vertex from '../../shaders/vertex.glsl'

class Cylinder
{
    constructor(experience)
    {
        this.experience = experience
        this.scene = this.experience.scene

        this.count = 5
        this.thetaLength = (2 * Math.PI) / this.count / 2
        this.offset = this.thetaLength + 0.6

        this.meshesArray = []

        this.createParts()
    }

    createParts()
    {
        this.geo = new THREE.CylinderBufferGeometry(2,2,1,36,36,true,0,this.thetaLength)

        this.part1 = new THREE.Mesh(
            this.geo,
            new THREE.ShaderMaterial({
                side:THREE.DoubleSide,
                vertexShader:vertex,
                fragmentShader:fragment,
                uniforms:{
                    uTime:{value:0},
                    uTexture:{value:this.experience.resources.items.texture1},
                    uTexture2:{value:this.experience.resources.items.texture22},
                    uColor:{value:0},
                    uImageTransition:{value:0}
                }
            })
        )

        this.part2 = new THREE.Mesh(
            this.geo,
            new THREE.ShaderMaterial({
                side:THREE.DoubleSide,
                vertexShader:vertex,
                fragmentShader:fragment,
                uniforms:{
                    uTime:{value:0},
                    uTexture:{value:this.experience.resources.items.texture2},
                    uTexture2:{value:this.experience.resources.items.texture22},
                    uColor:{value:0},
                    uImageTransition:{value:0}
                }
            })
        )
        this.part2.rotation.y = this.offset

        this.part3 = new THREE.Mesh(
            this.geo,
            new THREE.ShaderMaterial({
                side:THREE.DoubleSide,
                vertexShader:vertex,
                fragmentShader:fragment,
                uniforms:{
                    uTime:{value:0},
                    uTexture:{value:this.experience.resources.items.texture3},
                    uTexture2:{value:this.experience.resources.items.texture33},
                    uColor:{value:0},
                    uImageTransition:{value:0}
                }
            })
        )
        this.part3.rotation.y = this.offset * 2

        this.part4 = new THREE.Mesh(
            this.geo,
            new THREE.ShaderMaterial({
                side:THREE.DoubleSide,
                vertexShader:vertex,
                fragmentShader:fragment,
                uniforms:{
                    uTime:{value:0},
                    uTexture:{value:this.experience.resources.items.texture4},
                    uTexture2:{value:this.experience.resources.items.texture44},
                    uColor:{value:0},
                    uImageTransition:{value:0}
                }
            })
        )
        this.part4.rotation.y = this.offset * 3

        this.part5 = new THREE.Mesh(
            this.geo,
            new THREE.ShaderMaterial({
                side:THREE.DoubleSide,
                vertexShader:vertex,
                fragmentShader:fragment,
                uniforms:{
                    uTime:{value:0},
                    uTexture:{value:this.experience.resources.items.texture5},
                    uTexture2:{value:this.experience.resources.items.texture22},
                    uColor:{value:0},
                    uImageTransition:{value:0}
                }
            })
        )
        this.part5.rotation.y = this.offset * 4

        this.experience.world.group.add(this.part1, this.part2, this.part3, this.part4, this.part5)
        this.meshesArray.push(this.part1, this.part2, this.part3, this.part4, this.part5)

        for(const mesh of this.meshesArray)
        {
            mesh.visible = false
        }
    }

    update()
    {
    }
}

export default Cylinder