import * as THREE from 'three'

class Model
{
    constructor(experience)
    {
        this.experience = experience

        this.resource = this.experience.resources.items.model

        this.createModel()
        this.setAnimation()
    }
    
    // Add model to scene
    createModel()
    {
        this.model = this.resource.scene
        this.model.scale.set(0.5,0.5,0.5)
        this.model.position.y = -0.41

        this.model.traverse((child)=>
        {
            if(child instanceof THREE.Mesh && child.material instanceof THREE.MeshStandardMaterial)
            {
                // child.material.color = new THREE.Color(0x000011)
            }
        })


        this.experience.world.group.add(this.model)

        this.model.children[0].children[0].children[0].children[0].visible = false
        this.model.children[0].children[0].children[0].children[1].visible = false
    }

    setAnimation()
    {
        this.animation = {}
        this.animation.mixer = new THREE.AnimationMixer(this.model)

        this.animation.actions = {}
        this.animation.actions.walk = this.animation.mixer.clipAction(this.resource.animations[0])
        
        this.animation.actions.walk.play()
    }

    update()
    {
        this.animation.mixer.update(this.experience.time.delta * 0.0005)
    }
}

export default Model