import gsap from 'gsap'

class Links
{
    constructor(experience)
    {
        this.experience = experience

        this.links = document.querySelectorAll('.links')
        this.camera = this.experience.camera.instance
        this.group = this.experience.world.group

        this.createClicks()
    }

    createClicks()
    {
        for(const link of this.links)
        {
            link.addEventListener('click',()=>
            {
                if(link.id == 1)
                {
                    gsap.to(this.camera.position, {duration:1.5, x:0, y:0, z:0, ease: "circ.out"})
                }
                else if(link.id == 2)
                {
                    gsap.to(this.group.rotation, {duration:1.5, y:1, ease: "circ.out"})
                }
                else if(link.id == 3)
                {
                    gsap.to(this.group.rotation, {duration:1.5, y:2, ease: "circ.out"})
                }
                else if(link.id == 4)
                {
                    gsap.to(this.camera.position, {duration:1.5, x:-1, z:-1, ease: "circ.out"})
                }
                else if(link.id == 5)
                {
                    gsap.to(this.camera.position, {duration:1.5, x:-1, z:0, ease: "circ.out"})
                }
            })
        }
    }
}

export default Links