class ScrollCall 
{
    constructor()
    {
        this.text = document.querySelector('.scroll-text')

        this.rotateText()
    }

    rotateText()
    {
        this.text.innerHTML = this.text.textContent.replace(/\S/g, "<span>$&</span>")
        const element = document.querySelectorAll('span')

        for(let i=0; i<element.length; i++)
        {
            element[i].style.transform = `rotate(${i*32}deg)`
        }
    }
}

export default ScrollCall