import * as THREE from 'three'
import {Text} from 'troika-three-text'

class Font
{
    constructor(experience)
    {
        this.experience = experience

        this.createHeader()
        this.createAbout()
    }

    createHeader()
    {
        this.header = new Text()       

        this.header.text = `Contact`
        this.header.textAlign = 'center'
        this.header.anchorX = 'center'
        this.header.anchorY = '60%'
        this.header.fontSize = 0.4
        this.header.outlineWidth = '2%'
        this.header.outlineColor = new THREE.Color(0x111111)
        this.header.color = new THREE.Color(0x111111)
        this.header.sync()
        this.header.rotation.y = Math.PI / 2
        this.header.position.set(0,-2,0)

        this.experience.scene.add(this.header)
    }

    createAbout()
    {
        this.about = new Text()       

        this.about.text = `
I am Antino Kila, a Creative Developer based in United Kingdom, 

I graduaded from the Technological Educational Institute of Cr-
ete, with a BSc in IT Enginnering and i specialize in developing
3D interactive websites that users enjoy to visit.

I use WEBGL, JAVASCRIPT, CSS and HTML to build small projects.For
bigger projects i use frameworks, libraries and preprocessors like
REACT and SASS.`
        this.about.textAlign = 'center'
        this.about.anchorX = 'center'
        this.about.anchorY = '60%'
        this.about.fontSize = 0.016
        this.about.color = new THREE.Color(0xffffff)
        this.about.rotation.y = Math.PI / 2
        this.about.position.x = 0.6

        this.about.visible = false
        this.experience.scene.add(this.about)
    }

    update()
    {
    }
}

export default Font
